import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '',
        component: () => import('@/views/index.vue'),
        meta: {
            title: 'aboutMe',
        },
    },
    {
        path: '/sacred',
        component: () => import('@/views/sacred.vue'),
        meta: {
            title: '彩蛋',
        },
    },
]
const whiteList = ['/', '/sacred']

const router = new VueRouter({
    routes
})
router.beforeEach((to, from, next) => {
    // 判断当前路由是否在白名单中
    if (whiteList.indexOf(to.path) !== -1) {
        // 如果在白名单中，则直接放行
        next()
    } else {
        // 如果不在白名单中，则需要进行鉴权
        next('/')
    }
})
export default router
